<template>
  <div class="artist-edit">
    <div class="artist-header">
      <div class="img-container">
        <img :src="artist.avatarUrl" :alt="artist.name" />
      </div>
      <div class="right-content">
        <div class="artist-name">
          <div class="name">{{ name }}</div>
          <p class="edit" @click="editName">编辑名称</p>
        </div>
      </div>
    </div>
    <div class="artist-body">
      <div class="desc">
        <span>简介:</span><span class="edit" @click="editDesc">编辑</span>
        <p>{{ desc }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ArtistEdit',
  data() {
    return {
      name: '',
      desc: ''
    };
  },
  props: {
    artist: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  created() {
    this.name = this.artist.name;
    this.desc = this.artist.description;
  },
  methods: {
    editName() {
      this.$prompt('请输入名称', '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputValue: this.name
      })
        .then((data) => {
          const { value } = data;
          this.name = value;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    editDesc() {
      this.$confirm('请输入简介', '', {
        showInput: true,
        inputType: 'textarea',
        inputValue: this.desc
      })
        .then((data) => {
          const { value } = data;
          this.desc = value;
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }
};
</script>

